<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">页面管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
       <router-link :to="{ path: '/page/create' }">添加页面</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="tableData" stripe style="width: 100%">
        <el-table-column prop="name" label="页面名称" />
        <el-table-column prop="name" label="轮播图连接" />
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small">
              <router-link class='el-button--text' :to="{ path: `/page/${scope.row.id}/detail` }">编辑</router-link>
            </el-button>
            <el-button  @click="deleteHandleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total" />
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      tableData: [],
      curPage: 1,
      pageSize: 20,
      total: 0
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.pageList(params).then((res) => {
        if (!res) return
        this.tableData = res.data.result_rows.pages || []
        this.total = res.data.result_rows.page.total_count
      })
    },
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
      this.queryList()
    },

    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
      this.queryList()
    },
    // 删除操作
    deleteHandleClick (row) {
      this.$confirm('删除将清空该页面信息，确定删除吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then((r) => {
        this.$http.detelePage(row).then(res => {
          if (!res) return
          this.$message.success('操作成功')
          this.queryList()
        })
      })
      .catch(() => {})
    }
  },
  created () {
    this.queryList()
  }
}
</script>

<style>
</style>
